.list {
  display: flex;
  justify-content: space-between;
}

.name,
.number {
  margin-right: 15px;
}

.buttonList {
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  width: 100px;
  height: 30px;
  background-color: rgb(184, 202, 236);
  border: none;
  cursor: pointer;
}

.button:hover,
.button:focus {
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
}
